import React, { Component } from "react";
import ReactApexChart from "react-apexcharts";
import { kFormatter, PREPEND_ZERO } from "../../../../../../utils";
import FilterModeration from "../filterModeration";
class AssetsTypeCountChart extends Component {
  constructor(props) {
    super(props);

    this.state = {
      series: [1, 1, 1],
      options: {
        dataLabels: {
          enabled: false,
        },
        legend: {
          show: false,
        },
        stroke: {
          curve: "smooth",
        },
        colors: ["#F8B861", "#546FED", "#EF45B1"],
        plotOptions: {
          pie: {
            donut: {
              stroke: 0,
              legend: {
                show: false,
              },
              labels: {
                show: true,
                total: {
                  label: "Loading...",
                  showAlways: true,
                  show: true,
                },
                value: {
                  show: false,
                },
              },
            },
          },
        },
        chart: {
          type: "donut",
          animations: {
            enabled: true,
            easing: "easeinout",
            speed: 800,
            animateGradually: {
              enabled: true,
              delay: 2000,
            },
            dynamicAnimation: {
              enabled: true,
              speed: 350,
            },
          },
        },
        labels: [  "Video","Image","Text"],
      },
      imagePost: 0,
      videoPost: 0,
      textPost: 0,
      selectedCollection: [],
    };
  }

  componentDidMount() {
    this.onLoadAnalyticGraph();
  }

  onLoadAnalyticGraph = (data) => {
    const { postTypeCount } = this.props;

    const postCount = [];
    const responseData = {
      total_assets: {
        video: postTypeCount[0].data[0],
        image: postTypeCount[0].data[1],
        text: postTypeCount[0].data[2],
      },
    };


    Object.keys(responseData.total_assets).map((item) => {
      postCount.push(responseData.total_assets[item]);
      this.setState({
        videoPost: responseData.total_assets["video"],
        imagePost: responseData.total_assets["image"],
        textPost: responseData.total_assets["text"],
      });
    });

    this.setState({
      series: postCount,
      options: {
        plotOptions: {
          pie: {
            donut: {
              labels: {
                show: true,
                total: {
                  label: "Total Post",
                  showAlways: true,
                  show: true,
                },
                value: {
                  show: true,
                },
              },
            },
          },
        },
      },
    });
  };

  render() {
    const { options, series, imagePost, textPost, videoPost } = this.state;
    const { dateFilter, selectedWallFeed } = this.props
 
    return (
      <>
        <ReactApexChart
          options={options}
          series={series}
          type="donut"
          className="assetsType w-100 m-auto"
          style={{ maxWidth: 340 }}
        />
        <div
          className="count-box-main d-flex justify-content-center w-100 m-auto mt-4">
         { imagePost ? 
        <FilterModeration isClass={'count-box-card'} dateFilter={dateFilter} selectedWallFeed={selectedWallFeed} link={`media=With Image`}>
          <div>
            <div
              className="count-box blue text-center"
              style={{ borderTop: "6px solid #546FED" }}
            >
              <p className="fs-6 fw-bold mb-2 text-dark">Images</p>
              <span className="text-gray-800 fw-bolder text-dark fs-4">
                { kFormatter(PREPEND_ZERO(imagePost))}
              </span>
            </div>
          </div></FilterModeration> : null }

          { videoPost ?   

        <FilterModeration isClass={'count-box-card'} dateFilter={dateFilter} selectedWallFeed={selectedWallFeed} link={`media=With Video`}> 
          <div>
            <div
              className="count-box yellow text-center"
              style={{ borderTop: "6px solid #F8B861" }}
            >
              <p className="fs-6 fw-bold mb-2 text-dark">Videos</p>
              <span className="text-gray-800 text-dark fw-bolder fs-4">
                { kFormatter(PREPEND_ZERO(videoPost))}
              </span>
            </div>
          </div></FilterModeration> : null }

        { textPost ?   
         <FilterModeration isClass={'count-box-card'} dateFilter={dateFilter} selectedWallFeed={selectedWallFeed} link={`media=Text Only`}> 
          <div>
            <div
              className="count-box pink text-center text-dark"
              style={{ borderTop: "6px solid #EF45B1" }}
            >
              <p className="fs-6 fw-bold mb-2">Texts</p>
              <span className="text-gray-800 fw-bolder fs-4 text-dark">
                { kFormatter(PREPEND_ZERO(textPost))}
              </span>
            </div>
          </div> </FilterModeration> : null }
        </div>
      </>
    );
  }
}

export default AssetsTypeCountChart;
